<template>
  <div id="app">
    <audio>
      <source src="./sounds/1.mp3" type="audio/mpeg">
    </audio>
    <audio>
      <source src="./sounds/2.mp3" type="audio/mpeg">
    </audio>
    <audio>
      <source src="./sounds/3.mp3" type="audio/mpeg">
    </audio>
    <audio>
      <source src="./sounds/4.mp3" type="audio/mpeg">
    </audio>
    <div class="simon">
      <div class="simon__container">
        <div class="blue q" data-name="1" v-on:click="Pick"></div>
        <div class="red q" data-name="2" v-on:click="Pick"></div>
        <div class="yellow q" data-name="3" v-on:click="Pick"></div>
        <div class="green q" data-name="4" v-on:click="Pick"></div>
      </div>
      <div class="simon__container">
        <div>
          <p>Текущий раунд: {{this.simonRaund}}</p>
          <button v-on:click="newGame('off')">Начать игру</button>
          <p
            v-if="!this.gameOver"
          >Вы проиграли</p>
        </div>
        <div class="options-game">
          <div class="options-game__elem">
            <p>Легкая игра</p>
            <input type="radio" name="cspeed" checked data-checked="1" v-on:click="Check">
          </div>
          <div class="options-game__elem">
            <p>Средняя игра</p>
            <input type="radio" name="cspeed" data-checked="2" v-on:click="Check">
          </div>
          <div class="options-game__elem">
            <p>Сложная игра</p>
            <input type="radio" name="cspeed" data-checked="3" v-on:click="Check">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data:()=>({
    sequenceMoves:[],
    simonColors:['1', '2', '3', '4'],
    simonCount:0,
    simonRaund:1,
    con:0,
    gameOver: true,
    gameSpeed: 1500
  }),
  methods:{
    newGame(proms){
      for(let i = 0; i < 4; i++){
        document.querySelectorAll('.q')[i].classList.add('active')
      }
      let item = this.simonColors[Math.floor(Math.random()*this.simonColors.length)];
      let template = () =>{
        this.sequenceMoves.push(item)
        function pass(pr) {
          let a = pr.b[pr.a]
          let b = document.querySelectorAll('.q')[a - 1]
          let aud = document.getElementsByTagName('audio')[a - 1]
          return function () {
            setTimeout(()=>{
              aud.pause()
              aud.currentTime = 0.0
              aud.play()
              b.classList.add("__white")
            },100)
            setTimeout(()=>{
              b.classList.remove("__white")
              console.log(pr.c)
            }, pr.c)
          }
        }

        for(this.con = 0; this.con < this.simonRaund; this.con++) {
          setTimeout(pass({a:this.con, b:this.sequenceMoves, c:this.gameSpeed}), this.con * this.gameSpeed);
        }
      }
      if(proms === 'off'){
        this.simonCount = 0
        this.simonRaund = 1
        this.con = 0
        this.sequenceMoves = []
        this.gameOver = true
        template()
      }else if(proms === 'on'){
        this.con = 0
        template()
      }
    },
    Pick(e){
      if(this.sequenceMoves.length > 0){
        let aud = document.getElementsByTagName('audio')[e.path[0].dataset.name - 1]
        aud.pause()
        aud.currentTime = 0.0
        aud.play()
        if(e.path[0].dataset.name === this.sequenceMoves[this.simonCount]){
          this.simonCount++
        }else if(e.path[0].className !== this.sequenceMoves[this.simonCount]){
          this.simonCount = 0
          this.simonRaund = 1
          this.sequenceMoves = []
          this.gameOver = false
          for(let i = 0; i < 4; i++){
            document.querySelectorAll('.q')[i].classList.remove('active')
          }
        }
        if(this.simonCount === this.simonRaund){
          this.simonCount = 0
          this.simonRaund++
          setTimeout(()=>{
            this.newGame('on')
          },750)
        }
      }
    },
    Check(e){
      if(e.path[0].dataset.checked === '1'){
        this.gameSpeed = 1500
      }else if(e.path[0].dataset.checked === '2'){
        this.gameSpeed = 1000
      }else if(e.path[0].dataset.checked === '3'){
        this.gameSpeed = 400
      }
    }
  }
}
</script>

